<template>
  <el-dialog class="restore-plan-modal"
             :before-close="beforeModalClose"
             @close="modalClosed"
             :visible.sync="modalVisible">
    <span slot="title">
      添加恢复计划
    </span>
    <el-form size="small"
             label-position="right"
             label-width="100px"
             :model="formData"
             :rules="rules"
             ref="restorePlanCreateForm">
      <el-row>
        <el-form-item label="计划名称"
                      prop="name">
          <el-input v-model="formData.name"></el-input>
        </el-form-item>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="恢复设备IP"
                        prop="hostIp">
            <el-input v-model="formData.hostIp"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item :label="detailInfoLabelName"
                        prop="detailInfo">
            <el-input v-model="formData.detailInfo"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item label="登录名"
                        prop="loginName">
            <el-input v-model="formData.loginName"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="登录密码"
                        prop="password">
            <input-toggle v-model="formData.password"></input-toggle>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item label="时间策略"
                    style="width: 100%">
        <el-radio-group v-model="formData.timeStrategy">
          <el-radio :label="Number(s)"
                    v-for="s in Object.keys(strategys)"
                    :key="s">{{ strategys[s] }}</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="恢复时间"
                    prop="singleTime"
                    v-show="formData.timeStrategy == 1">
        <el-date-picker type="datetime"
                        format="yyyy-MM-dd HH:mm:ss"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        v-model="formData.singleTime"></el-date-picker>
      </el-form-item>
      <el-form-item label="计划时间"
                    prop="startTime"
                    v-show="[2,3].indexOf(formData.timeStrategy) !== -1">
        <el-date-picker type="datetime"
                        format="yyyy-MM-dd HH:mm:ss"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        v-model="formData.startTime"></el-date-picker>
      </el-form-item>
      <el-form-item label="选择星期"
                    prop="weekPoints"
                    v-show="formData.timeStrategy == 2">
        <el-checkbox-group v-model="formData.weekPoints">
          <el-checkbox-button v-for="w in Object.keys(weekMapping)"
                              :label="Number(w)"
                              :key="w">{{ weekMapping[w] }}</el-checkbox-button>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="选择日期"
                    prop="datePoints"
                    v-show="formData.timeStrategy == 3">
        <el-select v-model="formData.datePoints"
                   multiple>
          <el-option v-for="day in Array.from(new Array(31), (val, index) => index + 1)"
                     :key="day"
                     :value="day"
                     :label="day + '号'"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item :label="`时间点${index+1}`"
                    style="width: 100%"
                    v-for="(p, index) in formData.timePoints"
                    :key="p.key"
                    v-show="[2,3].indexOf(formData.timeStrategy) !== -1">
        <el-time-select v-model="formData.timePoints[index].value"
                        :picker-options="{start: '00:00', end: '23:45', step: '00:15'}"></el-time-select>
        <el-button icon="el-icon-delete"
                   type="danger"
                   v-show="formData.timePoints.length !== 1"
                   @click="formData.timePoints.splice(index, 1)"></el-button>
        <el-button icon="el-icon-plus"
                   type="primary"
                   v-show="index + 1 === formData.timePoints.length"
                   @click="formData.timePoints.push({ value: '00:00', key: Date.now() })"></el-button>
      </el-form-item>

    </el-form>
    <span slot="footer">
      <el-button @click="cancelButtonClick">取消</el-button>
      <el-button type="primary"
                 @click="confirmBtnClick">确定</el-button>
    </span>
  </el-dialog>
</template>
<script>
import isEqual from 'lodash/isEqual';
import {
  restoreTimeStrategyMapping as strategys,
  weekMapping,
} from '../../utils/constant';
import { createRestorePlan as createSqlserverRestorePlan } from '../../api/sqlserver';
import { createRestorePlan as createOracleRestorePlan } from '../../api/oracle';
import modalMixin from '../mixins/restorePlanModalMixins';

const requestMapping = {
  sqlserver: createSqlserverRestorePlan,
  oracle: createOracleRestorePlan,
};

export default {
  name: 'RestorePlanCreateModal',
  mixins: [modalMixin],
  methods: {
    confirmBtnClick() {
      this.$refs.restorePlanCreateForm.validate(valid => {
        if (valid) {
          const { name, config } = this.pruneData(this.formData);
          requestMapping[this.type]({
            id: this.id,
            data: { name, config },
          })
            .then(res => {
              const { data: plan } = res.data;
              this.$emit('confirm', plan);
              this.modalVisible = false;
            })
            .catch(error => {
              this.$message.error(error);
              return false;
            })
            .then(() => {
              // 无论成功或失败 提交后清除验证状态
              this.$refs.restorePlanCreateForm.clearValidate();
            });
        } else {
          return false;
        }
      });
    },
    modalClosed() {
      this.formData = { ...this.originFormData };
      this.$refs.restorePlanCreateForm.clearValidate();
    },
  },
};
</script>
<style >
</style>



